.icon.icon-16.icon-advancedworkflowadmin {
  background-image: url("../images/workflow-menu-icon.png");
}

.field.date .message.required,
.field.datetime .message.required {
  position: relative;
  top: 10px;
}

/**
 * Embargo & expiry badge styling
 *
 * 3 different statuses - embargo, expiry, embargo & expiry (both assigned)
 * Note: CSS nesting convention based on SilverStripe admin styling
 */

.cms-tree.jstree span.badge.status-embargo,
.cms-tree.jstree span.badge.status-expiry,
.cms-tree.jstree span.badge.status-embargo_expiry {
  background-color: #ebfbff;
  color: #3fa142;
}

#cms-content-tools-CMSMain .cms-tree.jstree span.badge.status-embargo,
#cms-content-tools-CMSMain
  .cms-tree.jstree span.badge.status-expiry,
#cms-content-tools-CMSMain
  .cms-tree.jstree span.badge.status-embargo_expiry {
  -webkit-box-shadow: 0px 0px 6px 2px #ebfbff;
  box-shadow: 0px 0px 6px 2px #ebfbff;
}

.workflow-field-diff del {
  background-color: #FDD;
  color: #d40404;
  padding: 2px;
}

.workflow-field-diff ins {
  background-color: #DFD;
  padding: 2px;
  text-decoration: none;
}

.workflow-field {
  position: relative;
  padding-bottom: 0;
}

.workflow-field .workflow-field-header {
  background: #95A5AB;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px 3px 0 0;
}

.workflow-field .workflow-field-header h3 {
  background: #7F9198;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  margin: 0;
  padding: 8px;
}

.workflow-field .workflow-field-create {
  padding: 4px 8px;
}

.workflow-field .workflow-field-create .chzn-container {
  float: left;
  margin-right: 4px;
  width: 250px !important;
}

.workflow-field .workflow-field-create .chzn-container .chzn-single {
  border-color: rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  box-shadow: none;
}

.workflow-field .workflow-field-create .chzn-container .chzn-single-with-drop {
  border-radius: 3px 3px 0 0;
}

.workflow-field .workflow-field-create .ss-ui-button {
  border-color: rgba(0, 0, 0, 0.2);
}

.workflow-field .workflow-field-actions {
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-top: none;
  border-radius: 0 0 3px 3px;
  padding: 8px;
}

.workflow-field .workflow-field-actions .workflow-field-action {
  margin-bottom: 4px;
}

.workflow-field .workflow-field-actions .workflow-field-action:last-child {
  margin-bottom: 0;
}

.workflow-field .workflow-field-actions .workflow-field-action .workflow-field-action-disabled {
  opacity: 0.35;
  filter: Alpha(Opacity=35);
  cursor: default;
  /*
				 * These default styles are taken from .ss-ui-button (_forms.scss) so we don't end up with mousover/focus states on disabled buttons.
				 * @todo These styles need to be dynamcally included from CMS, they are obviousely "hard-coded" for now.
				*/
}

.workflow-field .workflow-field-actions .workflow-field-action .workflow-field-action-disabled.ui-state-hover,
.workflow-field .workflow-field-actions .workflow-field-action .workflow-field-action-disabled.ui-state-focus {
  text-decoration: none;
  background-color: #e6e6e6;
  background: url("data:image/svg+xml;base64,");
  background: -webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, #fff), color-stop(100%, #d9d9d9));
  background: -webkit-linear-gradient(#fff, #d9d9d9);
  background: -moz-linear-gradient(#fff, #d9d9d9);
  background: -o-linear-gradient(#fff, #d9d9d9);
  background: -ms-linear-gradient(#fff, #d9d9d9);
  background: linear-gradient(#fff, #d9d9d9);
  -webkit-text-shadow: #fff 0 1px 1px;
  -moz-text-shadow: #fff 0 1px 1px;
  text-shadow: #fff 0 1px 1px;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}

.workflow-field .workflow-field-actions .workflow-field-action-header {
  background: #D7DDE0;
  border: 1px solid #95A5AB;
  border-left: none;
  overflow: auto;
  padding: 2px 8px 2px 0;
}

.workflow-field .workflow-field-actions .workflow-field-action-header .workflow-field-action-icon,
.workflow-field .workflow-field-actions .workflow-field-action-header .workflow-field-action-drag {
  display: block;
  float: left;
}

.workflow-field .workflow-field-actions .workflow-field-action-header .workflow-field-action-drag {
  background: #95A5AB;
  cursor: move;
  margin: -2px 0;
  width: 4px;
  height: 33px;
}

.workflow-field .workflow-field-actions .workflow-field-action-header .workflow-field-action-icon {
  width: 16px;
  height: 16px;
  margin: 7px 8px 0 8px;
}

.workflow-field .workflow-field-actions .workflow-field-action-header h4 {
  float: left;
  margin: 7px 8px 0 0;
  min-width: 360px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.4);
}

.workflow-field .workflow-field-actions .workflow-field-action-transitions li {
  background: #F8F8F8;
  border: 1px solid #D7DDE0;
  border-left: none;
  border-top: none;
  overflow: auto;
  padding: 4px 4px 4px 0;
}

.workflow-field .workflow-field-actions .workflow-field-action-transitions li .workflow-field-action-drag {
  background: #D7DDE0;
  cursor: move;
  float: left;
  margin: -4px 0;
  width: 4px;
  height: 24px;
}

.workflow-field .workflow-field-actions .workflow-field-action-transitions li .workflow-field-transition-title {
  float: left;
  min-width: 390px;
  overflow: auto;
}

.workflow-field .workflow-field-actions .workflow-field-action-transitions li .ui-icon,
.workflow-field .workflow-field-actions .workflow-field-action-transitions li span {
  float: left;
}

.workflow-field .workflow-placeholder {
  margin-bottom: 4px;
  height: 32px;
}

.workflow-field .workflow-field-loading {
  background: rgba(0, 0, 0, 0.15) url("../images/spinner.gif") no-repeat center center;
  border-radius: 0 0 3px 3px;
  bottom: 0;
  display: none;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 999;
}

.cms table.ss-gridfield-table .btn-icon-download-csv {
  display: inline-block;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  margin: -2px 0 0 10px;
}

/**
 * CSS for timepicker widget
 * See: http://trentrichardson.com/examples/timepicker/
*/

.ui-timepicker-div .ui-widget-header {
  margin-bottom: 8px;
}

.ui-timepicker-div dl {
  text-align: left;
}

.ui-timepicker-div dl dt {
  height: 25px;
  margin-bottom: -25px;
}

.ui-timepicker-div dl dd {
  margin: 0 10px 10px 65px;
}

.ui-timepicker-div td {
  font-size: 90%;
}

.ui-timepicker-div .ui-tpicker-grid-label {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

.ui-timepicker-div .ui-timepicker-rtl {
  direction: rtl;
}

.ui-timepicker-div .ui-timepicker-rtl dl {
  text-align: right;
}

.ui-timepicker-div .ui-timepicker-rtl dl dd {
  margin: 0 65px 10px 10px;
}

.ss-gridfield .ss-gridfield-item .disabled a.action {
  opacity: 0.35;
  filter: Alpha(Opacity=35);
  cursor: default;
}




